/**
 * base64转路径
 * @param {Object} base64
 */
export function base64ToPath(base64) {
	const [, format] = /^data:image\/(\w+);base64,/.exec(base64) || [];

	return new Promise((resolve, reject) => {

		const fs = uni.getFileSystemManager()
		//自定义文件名
		if (!format) {
			reject(new Error('ERROR_BASE64SRC_PARSE'))
		}
		const time = new Date().getTime();
		let pre = prefix()




		const filePath = `${pre.env.USER_DATA_PATH}/${time}.${format}`

		fs.writeFile({
			filePath,
			data: base64.split(',')[1],
			encoding: 'base64',
			success() {
				resolve(filePath)
			},
			fail(err) {
				console.error(err)
				reject(err)
			}
		})









































	})
}

/**
 * 路径转base64
 * @param {Object} string
 */
export function pathToBase64(path) {
	if (/^data:/.test(path)) return path
	return new Promise((resolve, reject) => {



















		if (uni.canIUse('getFileSystemManager')) {
			uni.getFileSystemManager().readFile({
				filePath: path,
				encoding: 'base64',
				success: (res) => {
					resolve('data:image/png;base64,' + res.data)
				},
				fail: (error) => {
					console.error({error, path})
					reject(error)
				}
			})
		}
















	})
}



export function getImageInfo(path, useCORS) {
	const isCanvas2D = this && this.canvas && this.canvas.createImage
	return new Promise(async (resolve, reject) => {
		// let time = +new Date()
		let src = path.replace(/^@\//,'/')
		if (cache[path] && cache[path].errMsg) {
			resolve(cache[path])
		} else {
			try {

				if (isBase64(path) && (isCanvas2D ? isPC : true)) {
					src = await base64ToPath(path)
				}






			} catch (error) {
				reject({
					...error,
					src
				})
			}

			if(isCanvas2D && !isPC) {
				const img = this.canvas.createImage()
				img.onload = function() {
					const image = {
						path: img,
						width:  img.width,
						height:  img.height
					}
					cache[path] = image
					resolve(cache[path])
				}
				img.onerror = function(err) {
					reject({err,path})
				}
				img.src = src
				return
			}

			uni.getImageInfo({
				src,
				success: (image) => {
					const localReg = /^\.|^\/(?=[^\/])/;

					image.path = localReg.test(src) ?  `/${image.path}` : image.path;

					if(isCanvas2D) {
						const img = this.canvas.createImage()
						img.onload = function() {
							image.path = img
							cache[path] = image
							resolve(cache[path])
						}
						img.onerror = function(err) {
							reject({err,path})
						}
						img.src = src
						return
					}















					cache[path] = image
					resolve(cache[path])
				},
				fail(err) {
					console.error({err, path})
					reject({err,path})
				}
			})
		}
	})
}



























